/* -----------------------------------------
   SEARCH 
   /src/styles/components/search.scss
----------------------------------------- */
header.global-header{
    .search-container{
        align-self: center;
        margin-left: auto;
        width: 32%;
    
        form{
            margin-top: 0;
            margin-bottom: 0;
    
            .search-textfield {
    
                max-height: 38px;
                border-radius: 5px 0 0 5px !important; // Square borders on right side for iPhone
                border: 1px solid var(--bs-gray-600, $gray-600);
            
                &::placeholder {
                    color: var(--bs-black, $black);
                    opacity: 1;
                }
            
                
                &:focus {
                    @include outline(2px);
                    outline-offset: -2px;
                    box-shadow: none;
                }
              }
            
              .search-button {
                padding: 0 12px;
                border: 1px solid;
                border-radius: 0 5px 5px 0;
                min-height: 38px;
                outline-offset: -2px;
                color: $white;
                background-color: var(--bs-gray-600, $gray-600);
                border-color: var(--bs-gray-600, $gray-600);
            
                &:hover,
                &:focus {
                    @include outline(2px);
                    background-color: var(--bs-gray-800, $gray-800);
                    border-color: var(--bs-gray-800, $gray-800);
                }
              }
        }
      
    }
}